.myNavbar{
  display: flex;
  position: sticky; 
  top: 0;
  justify-content: space-between;
  align-items: center;
  padding: 0rem var(--pad1);
  left: 0;
  right: 0;
  z-index: 1024;
  border: 1px solid transparent;
    
};
.active {
  color: red;
}

.myNavbar li{
list-style: none;
padding: 1.3rem 20px;
}

.myNavbar ul{
display: flex;
}

.myNavbar a,.same{
text-decoration: none;
color:black;
cursor: pointer;
font-size: var(--font1);
font-family: var(--fontf1);
}

.brand span{
transform: rotateY(0deg);
position: relative;
display: inline-block;
transition:transform .5s ease-in-out  ;
animation: flip 2s infinite;
animation-delay: calc(.5s * var(--i))
}
@keyframes flip {
0%,80% {
  transform: rotateY(360deg) 
}
}
.brand{
font-family: var(--fontf1);
font-size: 2.2rem;
color: var(--secondary-color);
position: relative
};